import { keyboard } from "@testing-library/user-event/dist/keyboard";
import "./App.css";
import { useEffect, useRef, useState } from "react";

function App() {
  const [display, setDisplay] = useState("");
  const [activeKey, setActiveKey] = useState(null);

  const songQ = useRef(null);
  const songW = useRef(null);
  const songE = useRef(null);
  const songA = useRef(null);
  const songS = useRef(null);
  const songX = useRef(null);
  const songC = useRef(null);
  const songD = useRef(null);
  const songZ = useRef(null);

  const playSound = (key) => {
    const sounds = {
      q: { ref: songQ, label: "chauffage1" },
      w: { ref: songW, label: "chauffage2" },
      e: { ref: songE, label: "chauffage3" },
      a: { ref: songA, label: "chauffage4" },
      s: { ref: songS, label: "applaudir" },
      d: { ref: songD, label: "open-hh" },
      z: { ref: songZ, label: "kick-n-hat" },
      x: { ref: songX, label: "coup-de-pied" },
      c: { ref: songC, label: "ferme-hh" },
    };

    const sound = sounds[key];
    if (sound?.ref?.current) {
      sound.ref.current.play();
      setDisplay(sound.label);
      setActiveKey(key);
      setTimeout(() => {
        setActiveKey(null);
      }, 150);
    }
  };
  useEffect(() => {
    const handleKey = (e) => {
      const key = e.key.toLowerCase();
      playSound(key);
    };

    window.addEventListener("keydown", handleKey);
    return () => window.removeEventListener("keydown", handleKey);
  }, []);
  return (
    <div className="App" id="drum-machine">
      <div className="drum-container">
        <div
          className={`drum-pad ${activeKey === "q" ? "active" : ""}`}
          id="chauffage1"
          onClick={() => playSound("q")}
        >
          Q
          <audio
            ref={songQ}
            src="https://cdn.freecodecamp.org/testable-projects-fcc/audio/Heater-1.mp3"
            className="clip"
            id="Q"
          ></audio>
        </div>
        <div
          className={`drum-pad ${activeKey === "w" ? "active" : ""}`}
          id="chauffage2"
          onClick={() => playSound("w")}
        >
          W
          <audio
            ref={songW}
            src="https://cdn.freecodecamp.org/testable-projects-fcc/audio/Heater-2.mp3"
            className="clip"
            id="W"
          ></audio>
        </div>
        <div
          className={`drum-pad ${activeKey === "e" ? "active" : ""}`}
          id="chauffage3"
          onClick={() => playSound("e")}
        >
          E
          <audio
            ref={songE}
            src="https://cdn.freecodecamp.org/testable-projects-fcc/audio/Heater-3.mp3"
            className="clip"
            id="E"
          ></audio>
        </div>
        <div
          className={`drum-pad ${activeKey === "a" ? "active" : ""}`}
          id="chauffage4"
          onClick={() => playSound("a")}
        >
          A
          <audio
            ref={songA}
            src="https://cdn.freecodecamp.org/testable-projects-fcc/audio/Heater-4_1.mp3"
            className="clip"
            id="A"
          ></audio>
        </div>
        <div
          className={`drum-pad ${activeKey === "s" ? "active" : ""}`}
          id="applaudir"
          onClick={() => playSound("s")}
        >
          S
          <audio
            ref={songS}
            src="https://cdn.freecodecamp.org/testable-projects-fcc/audio/Heater-6.mp3"
            className="clip"
            id="S"
          ></audio>
        </div>
        <div
          className={`drum-pad ${activeKey === "d" ? "active" : ""}`}
          id="open-hh"
          onClick={() => playSound("d")}
        >
          D
          <audio
            ref={songD}
            src="https://cdn.freecodecamp.org/testable-projects-fcc/audio/Dsc_Oh.mp3"
            className="clip"
            id="D"
          ></audio>
        </div>

        <div
          className={`drum-pad ${activeKey === "z" ? "active" : ""}`}
          id="kick-n-hat"
          onClick={() => playSound("z")}
        >
          Z
          <audio
            ref={songZ}
            src="https://cdn.freecodecamp.org/testable-projects-fcc/audio/Kick_n_Hat.mp3"
            className="clip"
            id="Z"
          ></audio>
        </div>
        <div
          className={`drum-pad ${activeKey === "x" ? "active" : ""}`}
          id="coup-de-pied"
          onClick={() => playSound("x")}
        >
          X
          <audio
            ref={songX}
            src="https://cdn.freecodecamp.org/testable-projects-fcc/audio/Kick_n_Hat.mp3"
            className="clip"
            id="X"
          ></audio>
        </div>
        <div
          className={`drum-pad ${activeKey === "c" ? "active" : ""}`}
          id="ferme-hh"
          onClick={() => playSound("c")}
        >
          C
          <audio
            ref={songC}
            src="https://cdn.freecodecamp.org/testable-projects-fcc/audio/Cev_H2.mp3"
            className="clip"
            id="C"
          ></audio>
        </div>
      </div>
      <div className="result" id="display">
        {display}
      </div>
    </div>
  );
}

export default App;
